.note {
    border: 1px solid #A0A0A0;
    background: white;
    width: 200px;
    height: 225px;
    margin: 0;
    text-align: left;
}

.note p {
    overflow: hidden;
 } 

.title {
    font-weight: bold;
    margin: 3px 5px;
    padding-bottom: 5px;
    border-bottom: 1px solid #A0A0A0;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    white-space: nowrap;
}

.body {
    font-size: 0.95rem;
    padding: 0 4px;
    height: 138px;
    word-wrap: break-word;
    overflow: hidden;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
}

.note:hover {
    -webkit-box-shadow: 5px 10px 5px #D3D2D3;
            box-shadow: 5px 10px 5px #D3D2D3;
} 

.tag-text {
    display: -ms-flexbox;
    display: flex;
    font-size: 0.75rem;
    margin-left: 2px;
    white-space: nowrap;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
}


