.delete-modal-container {
    height: 100vh;
    width: 100vw;
    position: absolute;
    z-index: 100;
    background: #D8D8D8E6;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

.delete-modal {
    background: white;
    border: 1px solid black;
    height: 200px;
    width: 30%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
}

.delete-btn {
    color: white;
    background: #CA001A;
    border: 1px solid grey;
    height: 30px;
    width: 40%;
    margin: 15px 2.5%;
}

.no-btn {
    color: white;
    background: #24B8BD;
    border: 1px solid grey;
    height: 30px;
    width: 40%;
    margin: 15px 2.5%;
}


